@import '../variables/variables';

/*********************************************************************************
* Single post navigation
******************************************************************************** */
.universal__single-post-navigation {
  margin: 30px 0;
  border: 1px solid $sp-navigation-border-color;
  border-left: 0;
  border-right: 0;

  @include clearfix();

  .universal__pagination {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      display: inline-block;
      padding: 5px 0;

      &.universal__pagination-next {
        margin-left: 15px;
      }

      a {
        color: $link-color;
        text-decoration: none;

        &:hover {
          color: $link-hover-color;
        }
      }
    }
  } // .universal__pagination
} // .universal__single-post-navigation



